/**
 * 
 * @param {String} str
 * @returns  //....
 */
export default function formatDate(str: any) {
  var arrTimestamp:any = (str + '').split('')
  for (var start = 0; start < 13; start++) {
    if (!arrTimestamp[start]) {
      arrTimestamp[start] = '0'
    }
  }
  str = arrTimestamp.join('') * 1
  var minute = 1000 * 60
  var hour = minute * 60
  var day = hour * 24
  var month = day * 30
  var now = new Date().getTime()
  var diffValue = now - str

  // 
  if (diffValue < 0) {
      return '';
  }
  // 
  var monthC: number = diffValue / month
  var weekC: number = diffValue / (7 * day)
  var dayC: number = diffValue / day
  var hourC: number = diffValue / hour
  var minC: number = diffValue / minute

  // 0
  var zero = function (value: number) {
    if (value < 10) {
      return '0' + value
    }
    return value
  };

  // 
  if (monthC > 4) {
    // 1
    return (function () {
      var date = new Date(str);
      return date.getFullYear() + '' + zero(date.getMonth() + 1) + '' + zero(date.getDate()) + '';
    })()
  } else if (monthC >= 1) {
    return parseInt(monthC.toString()) + ""
  } else if (weekC >= 1) {
    return parseInt(weekC.toString()) + ""
  } else if (dayC >= 1) {
    return parseInt(dayC.toString()) + ""
  } else if (hourC >= 1) {
    return parseInt(hourC.toString()) + ""
  } else if (minC >= 1) {
    return parseInt(minC.toString()) + ""
  }
  return '';
}

/**
 * 
 * @param {String} str
 * @returns  202023
 */
export function formatTime(timestamp: any) {
  let time = new Date(timestamp)
  let year = time.getFullYear()
  let month = time.getMonth() + 1 < 10 ? '0' + (time.getMonth() + 1) : time.getMonth() + 1
  let date = time.getDate() < 10 ? '0' + time.getDate() : time.getDate()
  return year + '' + month + '' + date + ' '
}